<template>
  <div id="app">
    <h1>在线翻译</h1>
    <h3 class="text-muted">简单 / 易用 / 便捷</h3>
    <translation v-on:formSubmit="translateText"></translation>
    <translation-output v-text="translationed"></translation-output>
    <small>开源精神，任意分享 @ 安职院 Cooper</small>
  </div>
</template>

<script>
import translation from "./components/Translation";
import translationOutput from "./components/TranslateOutput";
export default {
  name: "app",
  components: {
    // HelloWorld
    translation,
    translationOutput
  },
  data() {
    return {
      translationed: ""
    };
  },
  methods: {
    translateText: function(formSubmit, language) {
      // console.log(formSubmit)
      this.$http
        .get(
          "https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20181201T013547Z.4c94ee83398a3406.f9e39161703b921d64fbd3679f702bc012782dc7&lang=" +
            language +
            "&text=" +
            formSubmit
        )
        .then(respons => {
          // console.log(respons.body.text[0]);
          this.translationed = respons.body.text[0];
        });
    }
  }
};
</script>

